<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataResult"
			:items-per-page="10">
	    <template v-slot:item.no="{item}">
	    	{{dataPelajaran.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
	    <template v-slot:item.jenjang="{item}">
	    	{{item.jenjang.map(x => x.nama).join(',')}}    	
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<EditPelajaran :data="item" />
				<HapusPelajaran :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	import EditPelajaran from '@/components/Pelajaran/EditPelajaran'
	import HapusPelajaran from '@/components/Pelajaran/HapusPelajaran'
	export default{
		props: ['data'],

		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				{ text: 'Jenjang', value: 'jenjang', sortable: false },
				{ text: 'Jurusan', value: 'jurusan.nama', sortable: false },
				{ text: 'Kurikulum', value: 'kurikulum.nama', sortable: false },
				{ text: 'Kelas', value: 'kelas.nama', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			]
		}),

		components: {
			Loader,
			EditPelajaran,
			HapusPelajaran
		},

		created() {
			this.$store.dispatch('dataPelajaran/getDataPelajaran')
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataJurusan/getDataJurusan')
			this.$store.dispatch('dataPelajaran/updateIsLoading', true)
		},

		computed: {
			...mapState('dataPelajaran', ['dataPelajaran', 'isLoading']),
			dataResult() {
				return this.dataPelajaran
					.filter(mapel => mapel.kelas.id.toString().includes(this.data.kelas.toString()))
					.filter(mapel => mapel.nama.includes(this.data.mapel))
					.filter(mapel => mapel.kurikulum.nama.includes(this.data.kurikulum))
			}
		}
	}
</script>