<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        v-on="on"
      	icon 
      	color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Edit Pelajaran
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="data.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-model="data.kurikulum.id"
            :items="detailKurikulum"
            item-text="nama"
            item-value="id"
            label="Pilihan kurikulum"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="data.kelas.id"
            :items="detailKelas"
            item-text="nama"
            item-value="id"
            label="Pilihan kelas"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="data.jurusan.id"
            :items="detailJurusan"
            item-text="nama"
            item-value="id"
            label="Pilihan jurusan"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-for="(item, i) in data.jenjang"
            :key="i"
            v-model="detailJenjang[i]"
            :items="dataJenjang"
            item-text="nama"
            return-object
            label="Pilihan jenjang"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
            append-outer-icon="mdi-minus"
            @click:append-outer="minus(i)"
          ></v-select>
          <v-btn
            depressed
            color="success"
            block
            @click="data.jenjang.push('')"
            class="mb-6">
            tambah jenjang
          </v-btn>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            color="primary"
            :loading="isLoading"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import { flatten } from 'lodash'
  import Notif from '@/components/Notif'

  export default {
  	props: ['data'],

    data () {
      return {
        dialog: false,
        valid: true,
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil diedit',
          error: 'Data gagal diedit',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: { 
      ...mapState('dataPelajaran', ['status', 'isLoading']),
      ...mapState('dataJenjang', ['dataJenjang']),
      ...mapState('dataKurikulum', ['dataKurikulum']),
      ...mapState('dataJurusan', ['dataJurusan']),
      ...mapState('dataKelas', ['dataKelas']),
      detailJenjang: {
        get() {
          const hasil = this.data.jenjang.map(data => {
            return this.dataJenjang.filter(jenjang => jenjang.id === data.id)
          })
          return flatten(hasil)
        }
      },
      detailKurikulum: {
        set() {
          this.dataKurikulum.filter(k => k.id === this.data.kurikulum.id)
        },
        get() {
          return this.dataKurikulum
        }
      },
      detailKelas: {
        set() {
          this.dataKelas.filter(k => k.id === this.data.Kelas.id)
        },
        get() {
          return this.dataKelas
        }
      },
      detailJurusan: {
        set() {
          this.dataJurusan.filter(k => k.id === this.data.Jurusan.id)
        },
        get() {
          return this.dataJurusan
        }
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          let data = {
            id: this.data.id,
            nama: this.data.nama,
            kurikulum_id: this.data.kurikulum.id,
            kelas_id: this.data.kelas.id,
            jurusan_id: this.data.jurusan.id,
            jenjang_id: this.detailJenjang.map(x => x.id)
          }
          this.$store.dispatch('dataPelajaran/putDataPelajaran', data)
          this.$store.dispatch('dataPelajaran/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      },
      minus(val) {
        this.data.jenjang.splice(val, 1)
      }
    }
  }
</script>
