<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3"></v-icon>
			Daftar Pelajaran
		</v-card-title>
		<v-divider></v-divider>
		<v-card-text>
			<TambahPelajaran/>

			<v-form ref="form" v-model="valid" class="mt-6">
				<v-row dense>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Mata Pelajaran"
							outlined
							dense
							v-model="dataFilter.mapel"
							:items="dataPelajaran"
							item-text="nama"
							item-value="nama"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Kelas"
							outlined
							dense
							v-model="dataFilter.kelas"
							:items="dataKelas"
							item-text="nama"
							item-value="id"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Kurikulum"
							outlined
							dense
							v-model="dataFilter.kurikulum"
							:items="dataKurikulum"
							item-text="nama"
							item-value="nama"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-btn
							@click="reset"
							color="error"
							depressed>
							reset
						</v-btn>
					</v-col>
				</v-row>
			</v-form>

			<DataPelajaran :data="dataFilter" />
		</v-card-text>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	import DataPelajaran from '@/components/Pelajaran/DataPelajaran'
	import TambahPelajaran from '@/components/Pelajaran/TambahPelajaran'

	export default{
		data: () => ({
			valid: true,
			dataFilter: {
				mapel: '',
				kelas: '',
				kurikulum: ''
			}
		}),

		components: {
			TambahPelajaran,
			DataPelajaran
		},

		created() {
			this.$store.dispatch('dataKurikulum/getDataKurikulum')
			this.$store.dispatch('dataKelas/getDataKelas')
		},

		computed: {
			...mapState('dataPelajaran', ['dataPelajaran']),
			...mapState('dataKurikulum', ['dataKurikulum']),
			...mapState('dataKelas', ['dataKelas'])
		},

		methods: {
			reset () {
				this.dataFilter = {
					mapel: '',
					kelas: '',
					kurikulum: ''
				}
			}
		}
	}
</script>