<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mr-3"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Pelajaran
      </v-card-title>
      <v-divider></v-divider>
      <v-form v-model="valid" ref="form">
        <v-card-text class="pa-6 pb-0">
          <v-text-field
            v-model="dataPelajaran.nama"
            label="Nama"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-text-field>
          <v-select
            v-model="dataPelajaran.kurikulum_id"
            :items="dataKurikulum"
            item-text="nama"
            item-value="id"
            label="Pilihan kurikulum"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="dataPelajaran.kelas_id"
            :items="dataKelas"
            item-text="nama"
            item-value="id"
            label="Pilihan kelas"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-model="dataPelajaran.jurusan_id"
            :items="dataJurusan"
            item-text="nama"
            item-value="id"
            label="Pilihan jurusan"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
          ></v-select>
          <v-select
            v-for="(item, i) in dataPelajaran.jenjang_id.length"
            :key="i"
            v-model="dataPelajaran.jenjang_id[i]"
            :items="dataJenjang"
            item-text="nama"
            item-value="id"
            label="Pilihan jenjang"
            outlined
            dense
            :rules="requiredRule"
            autocomplete="off"
            append-outer-icon="mdi-minus"
            @click:append-outer="minus(i)"
          ></v-select>
          <v-btn
            depressed
            color="success"
            block
            @click="dataPelajaran.jenjang_id.push('')"
            class="mb-6">
            tambah jenjang
          </v-btn>

          <Notif :msg="msg" :status="status" @visible="visible" />
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false">
            kembali
          </v-btn>
          <v-btn
            :disabled="!valid"
            :loading="isLoading"
            color="primary"
            @click.prevent="validate"
            type="submit"
            depressed>
            simpan
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
  import { mapState } from 'vuex'
  import Notif from '@/components/Notif'

  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataPelajaran: {
          nama: '',
          jenjang_id: [''],
          kurikulum_id: '',
          kelas_id: '',
          jurusan_id: ''
        },
        item: ['foo', 'bar', 'zee'],
        requiredRule: [v => !!v || 'Data harus diisi'],
        msg: {
          success: 'Data berhasil ditambahkan',
          error: 'Data gagal ditambahkan',
          visible: false
        }
      }
    },

    components: { Notif },

    computed: {
      ...mapState('dataPelajaran', ['isLoading', 'status']),
      ...mapState('dataKurikulum', ['dataKurikulum']),
      ...mapState('dataJenjang', ['dataJenjang']),
      ...mapState('dataJurusan', ['dataJurusan']),
      ...mapState('dataKelas', ['dataKelas'])
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          this.$store.dispatch('dataPelajaran/postDataPelajaran', this.dataPelajaran)
          this.$store.dispatch('dataPelajaran/updateIsLoading', true)
          this.msg.visible = true
        }
      },
      visible(val) {
        this.msg.visible = val
      },
      minus(val) {
        this.dataPelajaran.jenjang_id.splice(val, 1)
      }
    }
  }
</script>
